.order-slider {
  @include sm {
    margin-top: 0 !important; }
  @include md {
    margin-top: 30px; }
  @include lg {
    margin-top: 90px; }
  .card {
    background-color: #dbe8e5; }

  h4.card-title {
    font-size: 30px;
    line-height: 1.2;
    font-weight: 400;
    color: #484848; }

  p.card-text {
    opacity: 0.9;
    font-size: 18px;
    line-height: 1.2;
    font-weight: 400;
    color: #3b3b3b; }

  .card-body {
    padding-top: 0; }
  .order-slider__glass {
    //padding: 48px 24px
    margin-top: 70px; }
  .order-slider__price {
    opacity: .5;
    font-size: 18px;
    line-height: 1.2;
    font-weight: 700;
    color: #6a6d6d; }

  .order-slider__title {
    opacity: .5;
    font-size: 18px;
    line-height: 1.2;
    font-weight: 700;
    color: #33587e; }

  .card-footer {
    background-color: #dbe8e5;
    padding: 0;
    border: 0; }

  .order-form__btn {
    //width: 239px
    //height: 58px
    opacity: 0.8;
    padding: 18px 44px 18px 64px;
    background-color: #2b5278;
    box-shadow: 0 9px 16px 0 #4b5681;
    font-size: 18px;
    line-height: 1.2;
    font-weight: 700;
    color: #e9e9e9;
    border-radius: 5px;
    position: relative;
    &:before {
      position: absolute;
      content: '';
      left: 20px;
      top: 50%;
      transform: translate(0, -50%);
      width: 31px;
      height: 28px;
      background: url("../img/boat.svg");
      color: white; } }

  .slider-block {
    position: relative;
    height: 535px; }
  .slider-arrow {
    position: absolute;
    //width: 111px
    z-index: 1;
    bottom: 0;
    right: 0;
    background-color: #fff;
    & img {
      cursor: pointer;
      width: 100px;
      height: 75px;
      padding: 20px 0; } } }



















