.list-block-about.faq {
  margin-top: 0;

  h4.card-title {
    font-size: 18px;
    //background-color: #ecf1f7
    padding: 22px 16px;
    color: #173454;
    &:nth-child(2n) {
      background-color: #dae5f1;
      @include md {
        background-color: #fff; } }
    &:nth-child(2n+1) {
      background-color: #ecf1f7;
      @include md {
        background-color: #fff; } }
    @include md {
      font-size: 25px; }
    @include lg {
      font-size: 48px; } }

  .card-text {
    font-size: 15px;
    padding: 22px 16px;
    @include md {
      font-size: 14px; }
    @include lg {
      font-size: 28px; } } }



