.header-bg-about {
  @include md {
    height: 173px; }
  @include lg {
    height: 360px; }
  overflow-y: hidden;
  & img {
    width: 100%;
    height: 99%;
    object-fit: cover;
    object-position: 75% 95%; }

  .wrapper-header {
    top: 0;
    bottom: 0;
    left: 0;
    right: 0; }

  .title {
    line-height: normal;
    font-weight: 700;
    color: #2b5278;
    @include md {
      font-size: 43px; }
    @include lg {
      font-size: 78px; } }

  .subtitle {
    line-height: 1.2;
    font-weight: 400;
    color: #1d1d1d;
    @include md {
      font-size: 13px;
      margin-top: 5px;
      margin-bottom: 10px; }
    @include lg {
      font-size: 24px;
      margin-top: 24px;
      margin-bottom: 60px; }
    span {
      font-weight: bold; } }

  .btn-select {
    opacity: 0.92;
    background-color: #2b5278;
    padding: 21px 49px 21px 73px;
    box-shadow: 0px 9px 16px 0px #4b5681;
    border-radius: 5px;
    font-size: 18px;
    line-height: 1.2;
    font-weight: 400;
    color: #e9e9e9;
    position: relative;
    text-decoration: none;
    &:before {
      position: absolute;
      content: '';
      left: 20px;
      top: 50%;
      transform: translate(0, -50%);
      width: 31px;
      height: 28px;
      background: url("../img/boat.svg"); } } }

.title-wrapper {
  min-height: 72vh; }

.text-box-mob {
  position: relative;
  margin-top: -15px;
  font-size: 18px;
  font-weight: 700;
  background-color: #fff;
  color: #173454;
  border-top-right-radius: 10px;
  border-top-left-radius: 10px;

  .title {
    background-color: #ecf1f7;
    margin: 22px 0 28px;
    font-size: 18px;
    line-height: 38px; }

  .subtext {
    font-size: 11px;
    line-height: 1.2;
    font-weight: 400;
    color: #1d1d1d;
    & span {
      font-weight: 700;
      color: #445584; }
    margin-bottom: 40px; } }

.text-box {
  bottom: 0;
  left: 50%;
  transform: translate(-50%,0);
  //position: absolute
  //bottom: -20px
  //left: 50%
  background: #fff;
  //transform: translate(-50%,0)
  //min-width: 1595px
  padding-top: 30px;
  .title {
    font-size: 33px;
    line-height: 1.2;
    font-weight: 700;
    color: #2b5278; }
  .subtext {
    font-size: 17px;
    line-height: 1.2;
    font-weight: 400;
    color: #3d3d3d;
    & span {
      font-weight: 700;
      color: #33597e; } }

  .header-banner {
    max-width: 1590px; }
  .text-box span.text-part {
    font-size: 17px;
    font-weight: 400;
    color: #3d3d3d; } }


