.service {
  .box-shadow {
    @include sm {
      box-shadow: none; }
    @include md {
      box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.15); } }

  .service__block-info {
    @include sm {
      padding: 0 15px 25px 15px; }
    @include md {
      padding: 50px 20px 40px; }
    @include lg {
      padding: 70px 60px 60px; } }
  .service__title {
    font-weight: 900;
    color: #1d1d1d;
    line-height: normal;
    @include sm {
      font-size: 18px; }
    @include md {
      font-size: 22px; }
    @include lg {
      font-size: 40px; }
    & .inner {
      color: #2b5278; } }

  .service__subtitle {
    font-weight: 300;
    color: #1d1d1d;
    margin: 57px 0 68px;
    @include sm {
      font-size: 11px;
      margin: 25px 0 21px; }
    @include md {
      font-size: 13px;
      margin: 32px 0 32px; }
    @include lg {
      font-size: 24px; }
    & span {
      font-weight: bold; } }

  .service__subtitle-add {
    opacity: 0.9;
    font-weight: 700;
    color: #1d1d1d;
    @include sm {
      font-size: 11px; }
    @include md {
      font-size: 11px; }
    @include lg {
      font-size: 20px; }
    & span {
      font-weight: bold; } }

  .service-img img {
    height: 100%;
    object-fit: cover; }

  .map {
    @include sm {
      min-height: 330px; }
    @include md;

    @include lg; }


  .service__block-contact {
    padding: 80px 70px;
    @include sm {
      padding: 40px 12px 0 12px; }
    @include md {
      padding: 40px 20px; }
    @include lg {
      padding: 80px 70px; } }

  .service__contact-title {
    line-height: 1.2;
    font-weight: 700;
    color: #1d1d1d;
    @include sm {
      font-size: 18px; }
    @include md {
      font-size: 22px; }
    @include lg {
      font-size: 40px; } }

  .service__contact-address,
  .service__contact-inn,
  .service__contact-workt,
  .service__contact-pay {
    font-weight: 300;
    color: #1d1d1d;
    & span {
      font-weight: 900;
      color: #2b5278; }
    @include sm {
      font-size: 13px;
      margin: 16px 0;
      font-weight: 400; }
    @include md {
      font-size: 14px;
      margin: 20px 0; }
    @include lg {
      margin: 35px 0;
      font-size: 24px; } } }


