.contact-form {
  //height: 532px
  background-image: url("../img/bg-form.png");
  background-repeat: no-repeat;
  background-size: 1920px 532px;
  min-height: 532px;
  background-position: 70% 50%;
  @media (min-width: 1920px) {
    background-size: cover; }

  .form-title {
    font-size: 47px;
    line-height: 1.2;
    font-weight: 700;
    color: #f1f1f1;
    margin-bottom: 20px; }

  .form-subtitle {
    font-size: 21px;
    line-height: 1.2;
    font-weight: 400;
    color: #c9e9ef; }

  .form-call-title {
    font-size: 20px;
    line-height: 1.2;
    font-weight: 400;
    text-transform: uppercase;
    color: #91e1ff; }

  .form-call-phone {
    font-size: 36.4px;
    line-height: 1.2;
    font-weight: 700;
    color: #d3ecff; }

  .form-call-input {
    outline: none;
    font-size: 16px;
    line-height: 1.2;
    font-weight: 400;
    color: #595c5d;
    padding: 25px;
    border-radius: 5px;
    border: 0; }

  .form-call-input::placeholder {
    color: #929292; }

  .form-send {
    margin-top: 21px; }

  .form__btn-send {
    font-size: 16px;
    line-height: 1.2;
    font-weight: 600;
    color: #464747;
    background-color: #ffdd2d;
    padding: 27px 45px;
    margin-left: 1rem;
    border-radius: 5px;
    &:hover {
      color: #fff;
      background-color: #57b5e1; } }

  .terms {
    font-size: 14px;
    line-height: 21px;
    font-weight: 400;
    color: #c9e9ef;
    margin-top: 32px; }

  .terms-link {
    font-size: 14px;
    line-height: 21px;
    font-weight: 500;
    color: #8dc3cd; }

  .w-42 {
    width: 42%; } }
















