.listpref {
  background-image: url("../img/bg-sky.png");
  min-height: 486px;
  background-size: 1920px 486px;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  margin-top: 62px;
  @media(min-width: 1920px) {
    background-size: cover; }

  h2.listpref__title {
    font-size: 43px;
    line-height: 1.2;
    font-weight: 400;
    color: #2d5685;
    text-align: center; }

  .listprefobj__title {
    background-color: #f5f5f6;
    width: 80px;
    height: 80px;
    font-size: 32px;
    line-height: 1;
    font-weight: 400;
    color: #333333;
    padding: 1.5rem;
    border-radius: 20px;
    opacity: .8;
    margin-bottom: 35px; }



  .listpref__subtitle {
    font-size: 17px;
    line-height: 1.2;
    font-weight: 400;
    color: #333333;
    padding: 20px 95px 30px;
    text-align: center; } }




