.catalog {
  @include sm {
    padding-top: 0; }
  @include md {
    padding-top: 20px; }
  @include lg {
    padding-top: 30px; }

  .card-body {
    padding: 0; }

  h5 {
    font-size: 17px;
    line-height: 1.2;
    font-weight: 700;
    color: #2b5278;
    @include md {
      font-size: 15px; }
    @include lg {
      font-size: 21px; } }
  & span {
    color: #5c5b5b;
    font-size: 12px;
    @include md {
      font-size: 11px; }
    @include lg {
      font-size: 15px; } }
  .card-title {
    font-size: 17px;
    line-height: 1.2;
    font-weight: 700;
    letter-spacing: 0.025em;
    color: #2b5278;
    margin-top: 29px;
    @include md {
      margin-top: 22px;
      margin-bottom: 0; }
    @include lg; }



  h4.card-title {
    color: #0d0d0d;
    font-size: 14px;
    margin-bottom: 25px;
    @include md {
      font-size: 12px;
      margin-top: 20px;
      margin-bottom: 16px; }
    @include lg {
      font-size: 17px; } }

  .testimonial {
    font-size: 10px;
    line-height: 1.2;
    font-weight: 500;
    color: #5c5b5b;
    margin-top: 29px;
    margin-bottom: 8px;
    display: block;
    @include md {
      font-size: 10px;
      margin-top: 0;
      margin-bottom: 15px; }
    @include lg {
      font-size: 13px;
      margin-bottom: 21px; } }

  .card-text {
    font-size: 12px;
    line-height: normal;
    font-weight: 500;
    color: #5c5b5b;
    padding: 10px;
    @include md {
      font-size: 10px; }
    @include lg {
      font-size: 14px; } }
  .year {
    position: relative;
    padding-left: 30px;
    &:before {
      position: absolute;
      content: '\f073';
      font-family: 'Font Awesome 5 Pro';
      font-size: 20px;
      top: 50%;
      transform: translate(0,-65%);
      left: 0;
      opacity: .8; } }

  .type {
    position: relative;
    padding-left: 20px;
    &:before {
      position: absolute;
      content: '';
      top: 4px;
      left: 0;
      width: 14px;
      height: 21px;
      background-image: url("../img/gripfire.svg");
      background-repeat: no-repeat;
      opacity: .5; } }

  .length {
    position: relative;
    padding-left: 20px;
    &:before {
      position: absolute;
      content: '\f07e';
      font-family: 'Font Awesome 5 Pro';
      font-size: 15px;
      top: 50%;
      transform: translate(0,-65%);
      left: 0; } }


  .amount {
    position: relative;
    padding-left: 20px;
    &:before {
      position: absolute;
      content: '\f1ae';
      font-family: 'Font Awesome 5 Pro';
      font-size: 15px;
      top: 50%;
      transform: translate(0,-65%);
      left: 0; } }

  .more {
    cursor: pointer; }
  .card-footer {
    background-color: #fff;
    border-radius: 5px;
    font-size: 13px;
    line-height: 1.2;
    text-transform: uppercase;
    color: #5c5b5b;
    padding: 17px 37px;
    opacity: 0.56;
    font-weight: 600;
    position: relative;
    border: 1px solid rgba(27,25,24,0.21);
    &:hover {
      background-color: #3c689b;
      color: #ffffff;
      opacity: 1;
      &:after {
        position: absolute;
        content: '';
        top: 50%;
        right: 26px;
        transform: translate(0, -50%);
        width: 13px;
        height: 22px;
        background-image: url("../img/chevron-right.png");
        background-repeat: no-repeat; } }
    @include sm {
      line-height: 0; }
    @include md {
      line-height: 0; }
    @include lg {
      line-height: 1.2; } }

  .arrow-slider {
    margin: 60px; }
  .arrow-right,
  .arrow-left {
    cursor: pointer;
    padding: 14px 22px;
    background: #e7eaed;
    border-radius: 10px;
    &:hover {
      background-color: #3c689b; } }
  .info {
    font-size: 15px;
    line-height: 1.2;
    font-weight: 700;
    color: #0d0d0d; }

  .show-more {
    margin: 40px 0 0;
    display: inline-block;
    text-align: center;
    font-size: 17px;
    font-weight: 700;
    color: #464747;
    line-height: 47px;
    width: 177px;
    border: 1px solid rgba(35,35,34,0.4);
    border-radius: 5px;
    @include md {
      line-height: 65px;
      margin: 70px 0; } } }

.catalog:not(.recomend) .col.mb-5.mb-sm-0 {
  display: none; }

.bg-catalog img {
  max-height: 230px;
  width: 100%;
  object-fit: cover;
  object-position: center 35%; }












