.recomend {
  .recomend-title {
    font-size: 18px;
    line-height: 1.2;
    font-weight: 700;
    color: #494a4b;
    margin: 15px 0;
    @include md {
      font-size: 24px;
      margin: 18px 0; }
    @include lg {
      font-size: 28px;
      margin: 38px 0; } }


  .tours-card .show-more-tours {
    margin: 40px 0 0;

    @include md {
      margin: 40px 0 0;
      line-height: 50px; }
    @include lg {
      margin: 40px 0 0; } }

  .card-title {
    margin: 0; }

  .label span {
    color: white; } }


