.tours-card {
  //overflow-x: hidden
  margin-top: -10px;
  padding-top: 16px;
  border-top-right-radius: 10px;
  border-top-left-radius: 10px;
  background-color: #fff;
  position: relative;

  .card {
    border: 0;
    box-shadow: 0 6px 18px -1px rgba(27, 25, 24, .15);

    .card-body {
      padding: 0; }

    .label-block {
      margin: 30px 0 20px 27px;
      @include md {
        margin: 30px 0 20px 28px; }
      @include lg {
        margin: 30px 0 30px 60px; } }

    span.card-title {
      font-size: 15px;
      line-height: 1;
      font-weight: 700;
      color: #1d1d1d;
      @include md {
        font-size: 13px; }
      @include lg {
        font-size: 24px; } }


    .label {
      font-size: 12px;
      line-height: 1;
      font-weight: 700;
      color: #ffffff;
      padding: 4px 20px 4px 15px;
      border-top-left-radius: 5px;
      border-bottom-left-radius: 5px;
      & span {
        font-size: 8px;
        @include md {
          font-size: 7px; }
        @include lg {
          font-size: 12px; } }
      @include md {
        font-size: 9px;
        padding: 4px 15px 4px 12px; }
      @include lg {
        font-size: 18px;
        padding: 8px 30px 8px 24px; } }


    p.card-text {
      font-size: 11px;
      line-height: 1.2;
      font-weight: 400;
      color: #4d4d4d;
      padding: 0 27px 30px;
      @include md {
        font-size: 9px;
        padding: 0 20px 20px 28px; }
      @include lg {
        font-size: 18px;
        padding: 0 40px 40px 60px; } }

    .bg-dark-blue {
      background-color: #2b5278; }

    .bg-blue {
      background-color: #156ac5; }

    .bg-green {
      background-color: #127ea4; }

    .bg-magenta {
      background-color: #b615c5; }

    .bg-red {
      background-color: #c51526; }

    .bg-dark-green {
      background-color: #83a412; } }

  .show-more-tours {
    margin: 40px 0 0;
    display: inline-block;
    text-align: center;
    font-size: 17px;
    font-weight: 700;
    color: #464747;
    line-height: 47px;
    width: 177px;
    border: 1px solid rgba(35,35,34,0.4);
    border-radius: 5px;
    @include md {
      line-height: 65px;
      margin: 70px 0; } }

  .col {
    display: none; } }



