.footer {
  background-image: url("../img/footer-bg.jpg");
  min-height: 103px;
  background-size: 1920px 103px;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  @media(min-width: 1920px) {
    background-size: cover; }

  .footer__copy {
    font-size: 18px;
    line-height: 1.2;
    font-weight: 400;
    color: #ffffff; }

  .footer__phone {
    font-size: 24px;
    line-height: 1.2;
    font-weight: 700;
    color: #ffffff; }

  .footer__wtime {
    font-size: 12px;
    line-height: 1.2;
    font-weight: 400;
    color: #ffffff; } }

.fa-facebook-f,
.fa-vk,
.fa-youtube,
.fa-telegram-plane,
.fa-instagram {
  &:before {
    color: #fff;
    font-size: 30px;
    @media(max-width: 1024px) {
      font-size: 20px; } } }
