.description111 {
  margin-top: 0;
  @include md {
    margin-top: 75px; }

  .all-tab-img img {
    padding-bottom: 20px; }

  .card {
    border: 0; }

  .card-body {
    @include md {
      padding: 1rem 0 30px 0; }
    @include lg {
      padding: 1rem 0 55px 0; } }


  .card-header {
    border: none;
    border-radius: 0; }

  .card-body i {
    color: #4b607b; }

  .all-tab-img img:nth-last-child(2) {
    padding-bottom: 0; }

  .ourwork-tab-wrap {
    padding: 0;
    position: relative;
    @include md {
      box-shadow: 0px 6px 18px -1px rgba(27,25,24,0.15);
      border: 1px solid rgba(27,25,24,0.15); } }


  .ourwork-tab-wrap .nav-tabs {
    display: none;
    border-bottom: none; }

  .ourwork-tab-wrap .nav-tabs .nav-item {
    display: flex;
    justify-content: center;
    align-items: center;
    flex: 1;
    border-right: 1px solid rgba(128,128,128,.25);
    border-bottom: 1px solid rgba(128,128,128,.25);
    & .nav-link {
        flex-grow: 1;
        align-self: stretch;
        //line-height: 50px
        display: flex;
        justify-content: center;
        align-items: center;
        @include md {
          height: 40px; }
        @include lg {
          height: 70px; } }
    &:last-child {
      border-right: 0;
      flex-grow: 0;
      .nav-link {
        padding: 0;
        width: max-content;
        &:hover {
          background-image: url('../img/testi-hover.png'); }
        @include md {
          height: 40px;
          width: 86px;
          background-image: url('../img/testi.png');
          background-position: center;
          background-size: contain; }
        @include lg {
          height: 70px;
          width: 152px;
          background-image: url('../img/testi.png');
          background-position: center;
          background-size: contain; }
        & img {
          height: 100%; } } } }

  .ourwork-tab-wrap .nav-tabs .nav-link {
    border: none;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
    font-size: 22px;
    line-height: 36px;
    font-weight: 300;
    color: #212121;
    text-align: center;
    @include md {
      font-size: 12px; }
    @include lg {
      font-size: 22px; } }

  .container-wrapper {
    padding: 0 16px;
    width: 100%;
    margin: 0 auto;
    @include md {
      padding: 0 40px; }
    @include lg {
      padding: 0 70px; } }

  .desc {
    font-weight: 300;
    color: #424242;
    @include md {
      font-size: 12px;
      line-height: normal;
      padding: 20px 0 30px; }
    @include lg {
      font-size: 22px;
      line-height: 36px;
      padding: 38px 0; } }


  .ourwork-tab-wrap .nav-tabs .nav-item.show .nav-link, .nav-tabs .nav-link.active {
    background: #ecf1f7; }


  .raiting {
    font-weight: 700;
    color: #424242;
    border-bottom: 9px solid #b6e7f8;
    @include md {
      font-size: 10px;
      line-height: 30px;
      border-bottom: 6px solid #b6e7f8; }
    @include lg {
      font-size: 18px;
      line-height: 30px; } }



  .btn-send__feed {
    font-weight: 700;
    width: 100%;
    line-height: 60px;
    font-size: 16px;
    color: #464747;
    border-radius: 5px;
    background: #ffdd2d;
    &:hover {
      background: #57b5e1;
      color: #fff; }
    @include md {
      line-height: 37px;
      font-size: 10px;
      font-weight: 700;
      width: 99px;
      margin-top: 25px; }
    @include lg {
      line-height: 68px;
      font-size: 16px;
      font-weight: 700;
      width: 177px;
      margin-top: 40px; } }

  .service-star, .comfort-star {
    font-size: 20px;
    font-weight: 400;
    color: #424242;
    @include md {
      font-size: 12px; }
    @include lg {
      font-size: 20px; } }


  form input {
    line-height: 60px;
    font-size: 16px;
    background: #ecf1f7;
    outline: none;
    border: 0;
    color: #595c5d;
    padding: 0 1rem;
    margin-right: 1.5rem;
    border-radius: 5px;
    &:last-child {
      margin-right: 0; }
    @include md {
      line-height: 37px;
      font-size: 10px;
      font-weight: 700; }
    @include lg {
      line-height: 68px;
      font-size: 16px;
      font-weight: 700; } }


  form textarea {
    background: #ecf1f7;
    outline: none;
    border: 0;
    font-size: 16px;
    font-weight: 700;
    color: #595c5d;
    padding: 1rem;
    margin-top: 1.5rem;
    @include md {
      line-height: normal;
      font-size: 10px;
      font-weight: 700; }

    @include lg {
      line-height: normal;
      font-size: 16px;
      font-weight: 700; } }

  .name span, .num, .time-tes {
    font-size: 12px;
    line-height: 20px;
    font-weight: 500;
    color: #424242;
    @include md {
      font-size: 12px; }
    @include lg {
      font-size: 22px; } }



  @media(min-width: 768px) {
    .ourwork-tab-wrap .nav-tabs {
      display: flex; }


    .ourwork-tab-wrap .card {
      border: none; }

    .ourwork-tab-wrap .card .card-header {
      display: none; }
    .ourwork-tab-wrap .card .collapse {
      display: block; } }


  @media(max-width: 767px) {
    .ourwork-tab-wrap .tab-content > .tab-pane {
      display: block !important;
      opacity: 1;
      &:nth-child(2n+1) {
        background: #ecf1f7; }
      &:nth-child(2n) {
        background: #dae5f1; }
 }      //margin-bottom: 10px

    .card-body {
      background-color: #fff; }


    .fa, .fas {
      font-weight: 500; }

    .num, .time-tes {
      font-size: 22px;
      line-height: 36px;
      font-weight: 500;
      color: #424242; }

    .ourwork-tab-wrap .card-header {
      color: #fff; }

    .ourwork-tab-wrap .card-header h5 a {
      color: #173454;
      border-bottom: none;
      text-decoration: none;
      font-size: 18px;
      line-height: 60px;
      display: flex;
      justify-content: space-between;
      align-items: center; }


    .ourwork-tab-wrap .card-header h5 a i {}

    .ourwork-tab-wrap .card-body {
 }      //border: 1px solid #427fad

    .ourwork-tab-wrap .card-header h5 a:hover {
      color: #173454; } } }



