.list-block.main-page {
  margin-top: 0;
  @include md {
    margin-top: 20px; }
  @include lg {
    margin-top: 90px; }
  .list-block__tite {
    font-size: 43px;
    line-height: 1.2;
    font-weight: 400;
    color: #2d5685; }
  .list-block__subtite {
    line-height: 1.2;
    font-weight: 700;
    color: #2d5685;
    font-size: 32px;
    text-transform: uppercase; }

  .list-block__input {
    outline: none;
    font-size: 16px;
    line-height: 1.2;
    font-weight: 400;
    color: #595c5d;
    padding: 25px;
    border-radius: 5px;
    border: 0;
    background-color: #e8edf3; }

  .list-block__btn {
    font-size: 16px;
    line-height: 1.2;
    font-weight: 600;
    color: #fff;
    background-color: #57b5e1;
    padding: 27px 45px;
    margin-left: 1rem;
    border-radius: 5px;
    &:hover {
      color: #464747;
      background-color: #ffdd2d; } }


  .terms {
    font-size: 13px;
    line-height: 21px;
    letter-spacing: .2px;
    font-weight: 400;
    color: #a9a9a9;
    margin-top: 28px; }

  .terms-link {
    font-size: 13px;
    line-height: 21px;
    font-weight: 400;
    color: #8dc3cd;
    letter-spacing: .2px; }

  .card-body {
    padding: 0; }

  .border-bottom {
    border-color: #b2b2b3; }

  h5 {
    font-size: 15px;
    line-height: 1.2;
    font-weight: 700;
    color: #1d1d1f;
    margin: 10px 0;
    padding-bottom: 10px; }

  a[href^="#collapse"] {
    cursor: default; }

  a.collapsed h4 {
    position: relative;
    &:after {
      position: absolute;
      content: '+';
      font-weight: 400;
      right: 20px;
      top: 50%;
      transform: translate(0, -50%); } }

  a.mob:not(.collapsed) h4 {
    position: relative;
    &:after {
      position: absolute;
      content: '-';
      right: 20px;
      top: 50%;
      transform: translate(0, -50%); } }


  h4 {
    font-size: 20px;
    line-height: 1.2;
    font-weight: 700;
    color: #173454;
    padding: 20px 0 20px 16px;
    @include md {
      padding: 20px 0; }
    @include lg {
      font-size: 25px; } }

  .card-text {
    font-size: 16px;
    line-height: 1.2;
    font-weight: 400;
    color: #3b3b3d; } }
