.header-bg-about.tours {
  overflow-y: unset;
  height: auto;
  margin-top: 0;
  padding-bottom: 16px;
  background-color: #aabbcf;
  @include md {
    margin-top: 60px;
    background-color: #fff; }
  @include lg {
    margin-top: 60px; }


  @media (max-width: 767px) {
    .title {
      font-size: 32px;
      line-height: 1.2;
      font-weight: 700;
      color: #2b5278;
      padding-top: 35px; }

    .subtitle {
      font-size: 12px;
      line-height: 1.2;
      font-weight: 400;
      color: #1d1d1d;
      padding-top: 20px;
      padding-bottom: 30px; } } }
