.header-bg.tour-page {
  height: 187px;
  @include md {
    height: 350px; }
  @include lg {
    height: 600px; }
  img.yaht-page {
    height: 100%;
    width: auto;
    object-fit: cover;
    border: 1px solid white;
    @include md {
      object-position: 10% top;
      max-height: 290px; }
    @include lg {
      object-position: left top;
      max-height: 100%; } }

  .main-title {
    padding-bottom: 0; }

  .title-main {
    color: #e4e6e9;
    font-size: 24px;
    font-weight: bold; }

  .subtitle {
    font-size: 14px;
    line-height: normal;
    color: #e4e6e9;
    font-weight: normal;
    margin-top: 0;
    margin-bottom: 0;
    @include md {
      font-size: 15px;
      margin-bottom: 120px;
      margin-top: 0; }
    @include lg {
      margin-bottom: 150px;
      font-size: 23px; } }

  .subtext {
    font-size: 33px;
    font-weight: 700;
    color: #2b5278;
    & span {
        font-size: 14px;
        @include lg {
          font-size: 24px; } } }

  .text-box {
    @include sm {
      display: none; }
    @include md {
      display: block;
      bottom: 7px; }
    @include lg {
      bottom: 0; } } }




