.slider {
  padding-top: 30px;
  h5 {
    font-size: 20px;
    line-height: 1.2;
    font-weight: 700;
    color: #2b5278; }
  & span {
    color: #5c5b5b; }
  .card-title {
    font-size: 17px;
    line-height: 1.2;
    font-weight: 700;
    letter-spacing: 0.025em;
    color: #0d0d0d;
    margin: 14px 0 27px; }
  h4.card-title {
    margin: 22px 0 27px; }
  .testimonial {
    font-size: 13px;
    line-height: 1.2;
    font-weight: 500;
    color: #5c5b5b;
    margin: 22px 0;
    display: block; }
  .card-text {
    font-size: 14px;
    line-height: normal;
    font-weight: 500;
    color: #5c5b5b;
    padding: 10px; }
  .year {
    position: relative;
    padding-left: 30px;
    &:before {
      position: absolute;
      content: '\f073';
      font-family: 'Font Awesome 5 Pro';
      font-size: 20px;
      top: 50%;
      transform: translate(0,-65%);
      left: 0;
      opacity: .8;
      //width: 21px
      //height: 23px
 } }      //background-image: url("../img/calendar.png")

  .type {
    position: relative;
    padding-left: 20px;
    &:before {
      position: absolute;
      content: '';
      top: 4px;
      left: 0;
      width: 14px;
      height: 21px;
      background-image: url("../img/gripfire.svg");
      background-repeat: no-repeat;
      opacity: .5; } }

  .length {
    position: relative;
    padding-left: 20px;
    &:before {
      position: absolute;
      content: '\f07e';
      font-family: 'Font Awesome 5 Pro';
      font-size: 15px;
      top: 50%;
      transform: translate(0,-65%);
      left: 0;
      //background-image: url("../img/length.png")
 } }      //background-repeat: no-repeat

  .amount {
    position: relative;
    padding-left: 20px;
    &:before {
      position: absolute;
      content: '\f1ae';
      font-family: 'Font Awesome 5 Pro';
      font-size: 15px;
      top: 50%;
      transform: translate(0,-65%);
      left: 0;
      //background-image: url("../img/people.png")
 } }      //background-repeat: no-repeat

  .more {
    cursor: pointer; }
  .card-footer {
    background-color: #fff;
    border-radius: 5px;
    font-size: 13px;
    line-height: 1.2;
    text-transform: uppercase;
    color: #5c5b5b;
    padding: 17px 37px;
    opacity: 0.56;
    font-weight: 600;
    position: relative;
    border: 1px solid rgba(27,25,24,0.21);
    &:hover {
      background-color: #3c689b;
      color: #ffffff;
      opacity: 1;
      &:after {
        position: absolute;
        content: '';
        top: 50%;
        right: 26px;
        transform: translate(0, -50%);
        width: 13px;
        height: 22px;
        background-image: url("../img/chevron-right.png");
        background-repeat: no-repeat; } } }
  .arrow-slider {
    margin: 60px; }
  .arrow-right,
  .arrow-left {
    cursor: pointer;
    padding: 14px 22px;
    background: #e7eaed;
    border-radius: 10px;
    &:hover {
      background-color: #3c689b; } }
  .info {
    font-size: 15px;
    line-height: 1.2;
    font-weight: 700;
    color: #0d0d0d; } }

.swiper {
  width: auto;
 }  //height: 300px
.order-slider .swiper {
  width: 100%; }

.swiper-button-prev,
.swiper-button-next {
  display: none; }

.swiper-slider {
  overflow: hidden; }


.swiper-slide {
  padding: 0 5px; }











