.consult.tour-2 {
  margin-top: 0;
  @include lg {
    margin-top: 0; }

  .form-title {
    font-size: 22px;
    line-height: 1.2;
    font-weight: 700;
    color: #494a4b;
    margin: 20px 0;
    @include lg {
      font-size: 30px;
      margin: 25px 0; } }

  form input {
    background-color: #ecf1f7;
    border-radius: 5px;
    padding-left: 1rem;
    line-height: 50px;
    font-size: 12px;
    font-weight: 500;
    color: #595c5d;
    outline: 0;
    border: 0;
    @include lg {
      line-height: 66px;
      font-size: 16px; } }

  button {
    font-size: 12px;
    line-height: 50px;
    font-weight: 700;
    color: #464747;
    background-color: #ffdd2d;
    width: 135px;
    border-radius: 5px;
    margin-left: 1rem;
    &:hover {
      color: #fff;
      background-color: #57b5e1; }

    @include lg {
      line-height: 69px;
      font-size: 16px;
      width: 180px; } }

  .terms {
    font-size: 11px;
    line-height: 21px;
    font-weight: 500;
    color: #878787;
    margin-top: 20px;
    letter-spacing: -.1px;
    & a {
      color: #8dc3cd; }
    @include lg {
      margin-top: 32px;
      font-size: 14px; } }

  .text-title {
    font-size: 24px;
    line-height: 1.2;
    font-weight: 300;
    color: #1d1d1d;
    & span {
      font-weight: 700; }
    @include md {
      font-size: 12px; }
    @include lg {
      font-size: 24px; } } }









