.menu {
  background: #fff;
  left: -600px;
  //left: 0
  height: 100vh;
  position: fixed;
  width: 600px;
  padding: 30px 0 0 60px;
  z-index: 10;
  box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.2);
  @media(max-width: 667px) {
    width: 320px;
    padding: 10px 0;
    position: absolute;
    height: auto; }
  @media(max-width: 812px) {
    position: absolute;
    min-height: 100vh;
    padding-bottom: 20px; }

  .menu__header {
    @media(max-width: 667px) {
      padding: 10px 0 0 20px; } }

  .menu__nav {
    margin-bottom: 20px;
    & ul li a {
      padding: 5px 20px; }
    & ul li a[data-bs-toggle="collapse"] {
      position: relative;
      padding: 5px 20px;
      &:after {
        position: absolute;
        content: '-';
        padding-left: 28px;
        font-weight: normal;
        font-size: 20px;
        transform: translate(0px, -50%);
        top: 50%; }
      &.collapsed:after {
        position: absolute;
        content: '+';
        font-weight: normal;
        font-size: 20px;
        transform: translate(0px, -50%);
        top: 50%; } } }

  .menu__rasp {
    @media(max-width: 667px) {
      padding: 0 20px; } }


  .menu__close {
    color: #fff;
    background-color: #e7eaed;
    padding: 16px 50px;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
    position: relative;
    cursor: pointer;
    & a:hover {
      color: #fff; }
    &:before {
      position: absolute;
      content: '';
      top: 50%;
      left: 5%;
      transform: translate(50%, -50%);
      width: 17px;
      height: 28px;
      background: url('../img/arrow-left.png');
      @media(max-width: 667px) {
        background-size: 72%;
        background-repeat: no-repeat;
        background-position: center center; } }
    @media(max-width: 667px) {
      padding: 8px 33px; }
    &:hover {
      background: #445584; } }

  .menu__logo img {
    width: 130px; }

  .menu__body {
    margin-top: 20px;
    padding-right: 60px;
    @media(max-width: 667px) {
      padding-right: 0; } }

  .menu__nav {
    li a:hover {
      background-color: #f6f7f8;
      font-weight: 500;
      color: #2a5177; }

    a {
      font-size: 24px;
      color: #191919;
      &:hover {
        color: #191919; } } }


  .timing {
    font-size: 18px;
    line-height: normal;
    font-weight: 600;
    color: #424242;
    border-bottom: 9px solid #b6e7f8;
    padding-bottom: 9px;
    @media(max-width: 667px) {
      margin-bottom: 10px;
      font-size: 15px;
      border-bottom: 5px solid #b6e7f8; } }

  .title-span {
    font-size: 18px;
    line-height: normal;
    font-weight: 400;
    color: #424242;
    padding: 10px 0;
    @media(max-width: 667px) {
      font-size: 12.5px;
      padding: 5px 0; } }

  .subtitle-span {
    font-size: 18px;
    line-height: normal;
    font-weight: 700;
    color: #424242;
    padding: 10px 0;
    @media(max-width: 667px) {
      font-size: 12.5px;
      padding: 5px 0; } }

  .not-active {
    color: #a9abbb; }

  .menu__footer {
    padding-right: 60px;
    margin-top: 60px;
    @media(max-width: 667px) {
      padding-right: 20px;
      padding-left: 20px;
      margin-top: 20px; } }

  .list-block__input {
    outline: none;
    font-size: 16px;
    line-height: 1.2;
    font-weight: 400;
    color: #595c5d;
    padding: 24px;
    border-radius: 5px;
    border: 0;
    background-color: #e8edf3;
    @media(max-width: 667px) {
      padding: 0 20px;
      line-height: 60px; } }


  .list-block__btn {
    font-size: 16px;
    line-height: 1.2;
    font-weight: 600;
    color: #fff;
    background-color: #57b5e1;
    padding: 24px 45px;
    margin-left: 1rem;
    border-radius: 5px;
    @media(max-width: 667px) {
      width: 100%;
      margin-left: 0;
      margin-top: 22px;
      padding: 0 20px;
      line-height: 60px; } }

  .terms {
    font-size: 13px;
    line-height: 21px;
    letter-spacing: .2px;
    font-weight: 400;
    color: #a9a9a9;
    margin-top: 28px;
    @media(max-width: 667px) {
      margin-top: 25px;
      font-size: 12px; } }


  .terms-link {
    font-size: 13px;
    line-height: 21px;
    font-weight: 400;
    color: #8dc3cd;
    letter-spacing: .2px;
    @media(max-width: 667px) {
      font-size: 12px; } } }

