.header-bg {
  height: calc(100vh - 130px);
  max-height: 1085px;
  overflow-y: hidden;
  & img {
    width: 100%;
    height: 99%;
    object-fit: cover;
    object-position: 75% 95%; }

  //background-image: url("../img/bg-header.jpg")
  //height: calc(100vh - 180px)
  //min-height: 460px
  //max-height: 1085px


  //background-size: 100% auto

  //background-size: cover
  //background-position: 50% 95%
  //background-repeat: no-repeat

  .wrapper-header {
    top: 0;
    bottom: 0;
    left: 0;
    right: 0; }

  .main-title {
    padding-bottom: 120px; }

  .title {
    font-size: 51px;
    line-height: 1.2;
    font-weight: 700;
    color: #2b5278; }

  .subtitle {
    font-size: 22px;
    line-height: 1.2;
    font-weight: 400;
    color: #2b5278;
    margin-top: 24px;
    margin-bottom: 45px; }
  .btn-select {
    opacity: 0.92;
    background-color: #2b5278;
    padding: 21px 49px 21px 73px;
    box-shadow: 0px 9px 16px 0px #4b5681;
    border-radius: 5px;
    font-size: 18px;
    line-height: 1.2;
    font-weight: 400;
    color: #e9e9e9;
    position: relative;
    text-decoration: none;
    &:before {
      position: absolute;
      content: '';
      left: 20px;
      top: 50%;
      transform: translate(0, -50%);
      width: 31px;
      height: 28px;
      background: url("../img/boat.svg"); } } }

.title-wrapper {
  min-height: 72vh; }

.text-box-mob {
  position: relative;
  margin-top: -15px;
  font-size: 18px;
  font-weight: 700;
  background-color: #fff;
  color: #173454;
  border-top-right-radius: 10px;
  border-top-left-radius: 10px;

  .title {
    background-color: #ecf1f7;
    margin: 20px 0 20px; }

  .subtext {
    font-size: 14px;
    line-height: 1.2;
    font-weight: 400;
    color: #3d3d3d;
    & span {
      font-weight: 700;
      color: #33597e; }
    & .text-part {
      font-weight: 400;
      color: #3d3d3d; } } }



.text-box {
  bottom: 0;
  left: 50%;
  transform: translate(-50%,0);
  //position: absolute
  //bottom: -20px
  //left: 50%
  background: #fff;
  //transform: translate(-50%,0)
  //min-width: 1595px
  padding-top: 30px;
  .title {
    font-size: 33px;
    line-height: 1.2;
    font-weight: 700;
    color: #2b5278; }
  .subtext {
    font-size: 17px;
    line-height: 1.2;
    font-weight: 400;
    color: #3d3d3d;
    & span {
      font-weight: 700;
      color: #33597e; } }

  .header-banner {
    max-width: 1590px; }
  .text-box span.text-part {
    font-size: 17px;
    font-weight: 400;
    color: #3d3d3d; } }


