.rentform {
  margin-top: -110px;
  margin-bottom: 60px;
  input,
  textarea {
    outline: 0; }

  .rentform__shadow {
    box-shadow: 0px 10px 15px 0px rgba(34, 60, 80, 0.25);
    border-top: 8px solid #53769a;
    padding-top: 60px;
    padding-left: 25px;
    padding-right: 25px; }

  h2 {
    color: #454544; }

  .select-block input {
    background-color: #ecf1f7;
    border: 0;
    border-radius: 5px;
    font-size: 16px;
    line-height: 32px;
    font-weight: 400;
    color: #595c5d;
    padding: 15px 16px 13px;
    width: 100%; }

  textarea {
    background-color: #ecf1f7;
    border: 0;
    border-radius: 5px;
    font-size: 16px;
    line-height: 1.2;
    font-weight: 400;
    color: #595c5d;
    padding: 24px 16px;
    width: 100%;
    margin: 25px 0; }

  .select-block input:first-child {
    margin-right: 25px; }

  .multipleSelection {
    width: 100%;
    background-color: #BCC2C1;
    position: relative;
    cursor: pointer; }

  .inputActive,
  .inputActiveSecond {
    background-color: #ecf1f7;
    border: 1px solid rgb(255,255,255);
    //border: 1px solid rgba(35,35,34,0.23)
    border-radius: 5px;
    font-size: 16px;
    line-height: 30px;
    font-weight: 400;
    color: #595c5d;
    padding: 15px 16px 13px;
    margin-bottom: 16px;
    width: 100%;
    position: relative; }

  .overSelect,
  .overSelectSecond {
    position: relative;
    &:after {
      position: absolute;
      content: '';
      top: 40%;
      transform: translate(0, -50%);
      right: 20px;
      width: 20px;
      height: 12px;
      background: url('../img/arrow-down.png');
      opacity: .6;
      transition: 300ms ease all; } }

  .overSelect.active,
  .overSelectSecond.active {
    &:after {
      position: absolute;
      content: '';
      top: 30%;
      //transform: translate(0, -50%)
      transform: rotateX(180deg);
      right: 20px;
      width: 20px;
      height: 12px;
      background: url('../img/arrow-down.png');
      opacity: .6;
      transition: 300ms ease all; } }


  .selectBox,
  .selectBoxSecond {
    position: relative;
    background: #ffffff;
 }    //border: 1px solid white


  .selectBox select,
  .selectBoxSecond select {
    width: 100%;
    font-weight: bold; }


  .overSelect,
  .overSelectSecond {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0; }

  #checkBoxes {
    display: none;
    //border: 1px #8DF5E4 solid
    position: absolute;
    background-color: #fff;
    width: 100%;
    box-shadow: 0px 10px 15px 0px rgba(34, 60, 80, 0.25);
    z-index: 2; }

  #checkBoxesSecond {
    display: none;
    //border: 1px #8DF5E4 solid
    position: absolute;
    background-color: #fff;
    width: 100%;
    box-shadow: 0px 10px 15px 0px rgba(34, 60, 80, 0.25);
    z-index: 1; }

  #checkBoxes label,
  #checkBoxesSecond label {
    display: block;
    flex: 1;
    font-size: 12px;
    line-height: 1.2;
    font-weight: 400;
    color: #333333;
    padding: 7px 0 7px 20px;
    cursor: pointer; }

  #checkBoxes label:hover {
    background-color: #f6f7f8; }

  #checkBoxesSecond label:hover {
    background-color: #f6f7f8; }

  .custom-checkbox {
    position: absolute;
    z-index: -1;
    opacity: 0; }

  .custom-checkbox+label {
    display: inline-flex;
    align-items: center;
    user-select: none; }

  .custom-checkbox+label::before {
    content: '';
    position: relative;
    top: 0;
    left: -7px;
    display: inline-block;
    width: 16px;
    height: 17px;
    flex-shrink: 0;
    flex-grow: 0;
    border: 0;
    background-color: #ecf1f7;
    border-radius: 0.25em;
    margin-right: 0.5em;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: 50% 50%; }

  .custom-checkbox:checked+label::before {
    border-color: #ffdd2e;
    background-color: #ffdd2e;
    background-image: url('../img/check-icon.svg');
    background-size: 12px 12px; }

  .selectBox label {
    padding: 16px 0; }

  .selectBoxSecond label {
    padding: 16px 0; }

  .addition {
    font-size: 19px;
    line-height: 1.2;
    font-weight: 400;
    color: #2c2c2c;
    margin-bottom: 15px;
    & label {
      font-size: 16px;
      line-height: 1.2;
      font-weight: 400;
      color: #464747; } }


  .addition-btn label {
    cursor: pointer;
    &:before {
      left: 0; } }

  .border-secondary {
    border-color: #f1f2f3 !important; }

  .terms {
    font-size: 14px;
    line-height: 21px;
    letter-spacing: .2px;
    font-weight: 400;
    color: #626363;
 }    //margin-top: 28px

  .terms-link {
    font-size: 14px;
    line-height: 21px;
    font-weight: 400;
    color: #336fee;
    letter-spacing: .2px; }

  .list-block__btn {
    font-size: 16px;
    line-height: 1.2;
    font-weight: 600;
    color: #464747;
    background-color: #ffdd2d;
    padding: 24px  45px;
    margin-left: 1rem;
    border-radius: 5px;
    &:hover {
      color: #fff;
      background-color: #57b5e1; } }


  .terms-block {
    padding: 60px 0; }

  .timing {
    font-size: 18px;
    line-height: normal;
    font-weight: 600;
    color: #424242;
    border-bottom: 9px solid #b6e7f8;
    padding-bottom: 9px; }

  .title-span {
    font-size: 18px;
    line-height: normal;
    font-weight: 400;
    color: #424242;
    padding: 10px 0; }

  .subtitle-span {
    font-size: 18px;
    line-height: normal;
    font-weight: 700;
    color: #424242;
    padding: 10px 0; }

  .not-active {
    color: #a9abbb; } }













