.yaht-card {
  margin-top: -10px;
  padding-top: 20px;
  background-color: #fff;
  position: relative;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  @include md {
    box-shadow: 0px 5px 10px 2px rgba(34,60,80,0.15);
    padding-top: 0;
    margin-top: 10px; }
  @include lg {
    margin-top: 30px; }

  .title-mob {
    margin: 25px 0 10px; }

  .title-yaht {
    font-size: 16px;
    line-height: 1.2;
    font-weight: 900;
    color: #1d1d1d; }

  .subtitle-yaht {
    font-size: 16px;
    line-height: 1.2;
    font-weight: 900;
    color: #2b5278;
    & span {
      font-size: 12px; } }


  .card-text {
    font-size: 14px;
    line-height: 1.2;
    color: #5c5b5b;
    @include md {
      font-size: 8px; }
    @include lg {
      font-size: 15px; } }

  .year-build, .type-of, .manufacture {
    @include sm {
      margin: 10px 0; }
    @include md {
      margin: 0; } }

  .payment-mob {
    height: 55px;
    background-color: #00bf96;
    & .payment a {
      color: #fff;
      font-size: 16px;
      line-height: 1.2;
      font-weight: 700; } }




  .title-yaer, .title-class, .title-manuf {
    font-weight: 500;
    position: relative;
    padding-left: 30px;
    @include md {
      padding-left: 20px; }
    @include lg {
      padding-left: 30px; } }

  .title-yaer:before {
    position: absolute;
    content: '\f073';
    font-family: 'Font Awesome 5 Pro';
    font-size: 20px;
    top: 50%;
    left: 0;
    transform: translate(0,-50%);
    @include md {
      font-size: 14px; }
    @include lg {
      font-size: 20px; } }

  .title-class:before {
    position: absolute;
    content: '';
    background: url("../img/svg/gripfire.svg");
    width: 17px;
    height: 24px;
    opacity: .6;
    top: 50%;
    left: 0;
    transform: translate(0,-50%);
    @include md {
      width: 17px;
      height: 15px;
      background-repeat: no-repeat;
      left: 5px; }
    @include lg {
      width: 17px;
      height: 24px;
      left: 0; } }

  .title-manuf:before {
    position: absolute;
    content: '';
    background-image: url("../img/boat-ico.svg");
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    width: 17px;
    height: 24px;
    top: 50%;
    left: 0;
    transform: translate(0,-50%);
    @include md {
      width: 17px;
      height: 15px;
      background-repeat: no-repeat; }
    @include lg {
      width: 17px;
      height: 24px; } }


  .year, .class, .manuf {
    font-weight: 400; }

  .border-right {
    border-right: 0;
    background-color: #ecf1f7;
    @include sm {
      border: 0; }
    @include md {
      border-right: 1px solid #e2e2e2;
      background-color: #fff; } }

  .border-bottom-line {
    border-bottom: 0;
    @include sm {
      border: 0; }
    @include md {
      border-bottom: 1px solid #e2e2e2; } }

  .box-ttx__title {
    width: 100%;
    margin: 0 auto;
    padding: 0 12px; }

  .text-descr {
    border-bottom: 0;
    width: 100%;
    margin: 0 auto;
    //background-color: #ecf1f7
    @include sm {
      border: 0; }
    @include md {
      border-bottom: 1px solid #e2e2e2;
      padding: 0 20px; }
    @include lg {
      padding: 0 48px; } }

  .payment-padding {
    @include md {
      padding-right: 20px; }
    @include lg {
      padding-right: 48px; } }

  .payment {
    @include md {
      padding-left: 20px; }
    @include lg {
      padding-left: 48px; }
    .img {
      width: 40px;
      height: 29px;
      position: relative;
      background-image: url('../img/you-ico.png');
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover; }
    &:hover {
      background-color: #455684;
      color: white;
      cursor: pointer;
      .img {
        background-image: url('../img/you-ico-white.png'); }
      & a {
        color: white; } } }


  .text-descr div {
    font-size: 14px;
    line-height: 1.2;
    font-weight: 300;
    color: #181717;
    @include md {
      font-size: 10px; }
    @include lg {
      font-size: 18px; } }

  .text-descr .col-7,
  .text-descr .col-5 {
    margin: 6px 0;
    @include md {
      margin: 6px 0; }
    @include lg {
      margin: 11px 0; } }


  .box-ttx {
    padding: 0;
    @include md {
      padding: 0 20px; }
    @include lg {
      padding: 0 48px; } }

  .order-box {
    //margin: 16px 0
    @include md {
      margin: 0 !important; }
    @include lg {
      margin: 0 !important; } }

  .box-second {
    background-color: #f7f9fc;
    @include md {
      background-color: #fff; }
    @include lg {
      background-color: #fff; } }

  .contact-form .form-title {
    margin-bottom: 0; }


  .form__btn-send {
    background-color: #445584 !important; }

  .order-input {
    //line-height: 67px
    border-radius: 5px;
    background-color: #ecf1f7;
    outline: 0;
    border: 0;
    padding-left: 32px;
    //margin-right: 24px
    //width: 236px
    @include md {
      width: 123px;
      line-height: 34px;
      padding-left: 17px;
      font-size: 9px;
      margin-right: 13px; }
    @include lg {
      width: 236px;
      line-height: 67px;
      font-size: 17px;
      margin-right: 24px; } }


  .order-btn {
    //font-size: 17px
    //width: 174px

    font-weight: 700;
    color: #464747;
    background-color: #ffdd2d;
    border-radius: 5px;
    @include md {
      width: 91px;
      line-height: 34px;
      font-size: 9px; }
    @include lg {
      width: 174px;
      line-height: 67px;
      font-size: 17px; } }


  //.payment
  //  //font-size: 17px
  //  font-weight: 700
  //  color: #455684
  //  @include md
  //    font-size: 9px
  //  @include lg
  //    font-size: 17px
  //  & img
  //    @include md
  //      width: 21px
  //      height: auto
  //    @include lg
  //      width: auto

  .payment {
    //font-size: 17px
    font-weight: 700;
    color: #455684;
    align-self: stretch;
    padding: 16px 2rem 16px 0;
    @include md {
      font-size: 9px;
      padding: 20px 1rem 20px 0;
      margin-right: 1rem;
      border-right: 1px solid #dee2e6; }
    @include lg {
      font-size: 17px;
      padding: 35px 2rem 35px 0;
      margin-right: 1rem; }
    & img {
      @include md {
        width: 21px;
        height: auto; }
      @include lg {
        width: auto; } } }


  .swiper-button-prev, .swiper-button-next {
    display: flex;
    color: #fff; } }






